.header {
    padding: 10px 0;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #f1f1f1
}


.user_img {
    width: 60px;
    height: 60px;
    margin-right: 10px;
    border-radius: 100%;
}

.name {
    font-size: 16px;
    color: #000;
    margin-bottom: 3px;
}

.name:hover {
    color: #000;
}

.date {
    font-size: 12px;
}

.others {
    margin-left: auto;
    display: flex;
    align-items: center;
}

.others i {
    cursor: pointer;
    margin-left: 20px;
    transition: all .1s ease;
}

.others i:hover {
    color: #000;
}
@media screen and (max-width:768px){
    .user_img{
        width: 40px;
        height: 40px;
    }
    .header{
        padding: 10px ;
    }
}