.chats {
    position: relative;
    min-width: 100%;
    max-width: 100%;
    background: #fff;
    height: 83vh;
    border-radius: 5px;
    box-shadow: 0 2px 6px 0 rgba(0,0,0,0.12), inset 0 -1px 0 0 #dadce0;
}
.preview{
    color: #000;
    margin: 0 auto;
    margin-top: 200px;
    text-align: center;
    font-size: 40px;
}
@media screen and (max-width:768px){
    .chats{
        height: auto;
    } 
}
