.message_wrapper {
    margin-top: 10px;
    width: 90%;
    display: flex;
}

.message {
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-end;
}

.message img {
    margin-right: 10px;
}

.my_message {
    display: flex;
    margin-left: auto;
}

.my_message img {
    margin-left: 10px;
}

.text {
    position: relative;
    background: #f5f5f5;
    min-width: 100%;
    line-height: 1.2em;
    padding: 10px;
    color: #000;
    font-weight: 300;
    font-size: 12px;
    border-radius: 5px;
    max-width: 60%;
    margin-top: 20px;
}
.text i{
    position: absolute;
    opacity: 0;
    right: 5px;
    top: 5%;
    cursor: pointer;
    font-size: 14px;
    color: #000;
    margin-left: 5px;
    transition: all .2s ease;
}
.text:hover i{
    opacity: .8;
}

.img_and_date {
    display: flex;
    flex-direction: column;
}

.date {
    text-align: center;
    font-size: 12px;
}

.mes_img {
    width: 50px;
    height: 50px;
    border-radius: 100%;
    margin-bottom: 10px;
}
.del_btn{
    
}
@media screen and (max-width:768px){
    .message_wrapper{
        width: 100%;
        margin-top: 0;
    }
    .img_and_date{
        display: none;
    }
}
